import { useLocation, useParams } from "react-router-dom";
import SquareTabCard from "../../../Ui/Tab/Square/SquareTabCard";
import SquareTabWrapper from "../../../Ui/Tab/Square/SquareTabWrapper";
import UserMemberTabsWrapper from "../../../Ui/UserMember/UserMemberTabsWrapper";
import {
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";

const InstituteAcademicDepartmentDetailProfile = () => {
  const params = useParams();
  const getQuery = useLocation();
  let measure = document.getElementById("562nbhgh")?.clientWidth;
  let height = "9rem";
  let width = "9rem";
  if (measure) {
    let disvible =
      measure > 820 ? (measure > 1100 ? 6 : 5) : measure > 700 ? 4 : 3;
    let mi = measure > 820 ? 0 : 2;
    width = `${Math.floor((measure - mi - 31 * disvible - 1) / disvible)}px`;
    height = width;
  }
  return (
    <>
      <UserMemberTabsWrapper
        customStyle={{
          border: "0",
        }}
      >
        <SquareTabWrapper dynamicId="562nbhgh">
          <SquareTabCard
            label="class"
            iconUrl={`${assestsUserFinanceUrl}/bank.svg`}
            url={`/q/${params.username}/institute/member/academic/department/detail?type=cls`}
            state={getQuery.state}
            isAuthorized
            isVisible={{ bank_details: true }}
            authorizedKey="bank_details"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
          <SquareTabCard
            label="masters"
            iconUrl={`${assestsUserFinanceUrl}/pending.svg`}
            url={`/q/${params.username}/institute/member/academic/department/detail?type=master`}
            state={getQuery.state}
            isAuthorized
            isVisible={{ bank_details: true }}
            authorizedKey="bank_details"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
          <SquareTabCard
            label="batches"
            iconUrl={`${assestsUserFinanceUrl}/pending.svg`}
            url={`/q/${params.username}/institute/member/academic/department/detail?type=batch`}
            state={getQuery.state}
            isAuthorized
            isVisible={{ bank_details: true }}
            authorizedKey="bank_details"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />

          <SquareTabCard
            label="staffs"
            iconUrl={`${assestsUserAdmissionUrl}/payment-verification.svg`}
            url={`/q/${params.username}/institute/member/academic/department/detail?type=staff`}
            state={getQuery.state}
            isAuthorized
            isVisible={{ bank_details: true }}
            authorizedKey="bank_details"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />

          <SquareTabCard
            label="students"
            iconUrl={`${assestsUserAdmissionUrl}/excess.svg`}
            url={`/q/${params.username}/institute/member/academic/department/detail?type=student`}
            state={getQuery.state}
            isAuthorized
            isVisible={{ bank_details: true }}
            authorizedKey="bank_details"
            customStyleContainer={{
              height: height,
              width: width,
            }}
          />
        </SquareTabWrapper>
      </UserMemberTabsWrapper>
    </>
  );
};

export default InstituteAcademicDepartmentDetailProfile;
