import { useState } from "react";
import { useOneDepartmenteDelete } from "../../../../../Department/Institute/Components/DepartmentApi/depart-api";
import ModalMenu from "../../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";

const InstituteTheoryClassDetailCardMenu = ({
  onClose,
  menuData,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [departmentDelete] = useOneDepartmenteDelete();

  const onDelete = () => {
    if (menuData?._id) {
      departmentDelete({
        did: menuData?._id,
      }).then((res) => {
        onRefetch();
        setDisabled((pre) => !pre);
        onClose();
      });
    }
  };
  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("remove")}</h6>
      </ModalMenu>
    </>
  );
};

export default InstituteTheoryClassDetailCardMenu;
