import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { useClassMasterAllStudent } from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import CreateButton from "../../../../Ui/Button/CreateButton";
import InstituteTheoryClassDetailCard from "./InstituteTheoryClassDetailCard";
import InstituteDepartmentAddStudent from "../InstituteDepartmentAddStudent/InstituteDepartmentAddStudent";
import InstituteTheoryClassDetailReshuffle from "./InstituteTheoryClassDetailReshuffle/InstituteTheoryClassDetailReshuffle";

const InstituteTheoryClassDetail = ({ clsId, openAs }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);
  const [reshuffle, setReshuffle] = useState(false);
  const {
    classMasterAllStudent,
    classMasterAllStudentLoading,
    classMasterAllStudentRefetch,
  } = useClassMasterAllStudent({
    data: {
      cid: clsId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !clsId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (classMasterAllStudent?.list?.length) {
      let obj = nextPage(classMasterAllStudent?.list?.length);
      setState(() => obj);
    }
  }, [classMasterAllStudent?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    classMasterAllStudentRefetch();
  }, [classMasterAllStudentRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onReshuffleToggle = () => {
    setReshuffle((pre) => !pre);
  };
  const onAddStudent = () => {};

  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("theory_classes")}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "200px",
                  }}
                />
              </div>
            </section>
            <CreateButton
              label={t("resuffle")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                minWidth: "fit-content",
              }}
              onAction={onReshuffleToggle}
            />
            <CreateButton
              label={t("add_student")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                minWidth: "fit-content",
              }}
              onAction={onToggle}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
        }}
      >
        {classMasterAllStudent?.list?.map((student, index) =>
          classMasterAllStudent?.list?.length === index + 1 ? (
            <div key={student?._id} ref={ref}>
              <InstituteTheoryClassDetailCard
                student={student}
                openAs={openAs}
              />
            </div>
          ) : (
            <InstituteTheoryClassDetailCard
              student={student}
              key={student?._id}
              openAs={openAs}
            />
          )
        )}
        {classMasterAllStudentLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          classMasterAllStudent?.list?.length === 0 && (
            <EmptyMessage title="No student found related this search." />
          )
        ) : (
          classMasterAllStudent?.list?.length === 0 && (
            <EmptyMessage title="No student." />
          )
        )}
      </UserMemberContentWrapper>

      {add && (
        <InstituteDepartmentAddStudent
          onClose={onToggle}
          clsId={clsId}
          onAdd={onAddStudent}
        />
      )}
      {reshuffle && (
        <InstituteTheoryClassDetailReshuffle
          onClose={onReshuffleToggle}
          clsId={clsId}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default InstituteTheoryClassDetail;
